.app {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  font-size: 50px;
  color: #ffffff;
}

span {
  color: #ffffff;
  margin: 12px 0;
}

.area-input {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.area-input input {
  width: 500px;
  margin-bottom: 12px;
  height: 46px;
  background-color: #121212;
  border: 0;
  border-radius: 6px;
  color: #ffffff;
  padding: 0px 12px;
  text-align: center;
  font-size: 18px;
}

.area-input button {
    font-weight: bold;
  width: 100%;
  height: 46px;
  border: 0;
  border-radius: 6px;
  background-color: #1e57ca;
  color: #ffffff;
  font-size: 18px;
}

.area-input button:hover {
    background-color: #1a4497;
}

h2 {
  margin-top: 28px;
} 
